.sideBar{@apply z-10 top-[65px] fixed overflow-y-scroll h-full w-[350px] px-4 pt-6 pb-[150px] bg-white border-r}@media(max-width: 1024px){.sideBar{@apply hidden}}.sideBar .sideBarSection{@apply font-semibold border-b}.sideBar .sideBarList .sideBarListItem{@apply list-disc ml-5}.sideBar::-webkit-scrollbar{@apply w-2}.sideBar::-webkit-scrollbar-track{@apply bg-slate-200 /* цвет дорожки */}.sideBar::-webkit-scrollbar-thumb{@apply bg-teal-700 cursor-pointer}